import React, { Component } from 'react';
import { View, Header, Title, Content } from 'library';
import { Filter } from 'library/util';

class Coupon extends Component {

  // 
  state = {};

  componentDidMount() {
    const { match } = this.props;
    axios.get('/account/ajax/coupon', {
      params: {
        code: match.params.code,
      },
      cache: true,
    }).then(({ data }) => {
      this.setState({
        coupon: data.entity,
      });
    });
  }

  renderSesc() {
    const { coupon } = this.state;
    if (coupon.templateType === 4) {
      let desc = coupon.desc || '';
      return desc.split('\n').map((item, idx) => (<p key={idx}>{item}</p>));
    } else {
      return (
        <section>
          <p>1. {coupon.productNoNames.join(',')}</p>
          <p>2. {coupon.isExclusive === 1 ? '' : ''}</p>
          <p>3. {coupon.isAllProject === 0 ? (coupon.projectName.join(',') || '') : ''}</p>
        </section>
      );
    }
  }

  render() {
    const TYPES = { 1: '', 2: '', 3: '', 4: '' };
    const STATUS = { 1: '', 2: '', 3: '', 4: '', 5: '', 6: '' };
    const { coupon } = this.state;
    return (
      <View>
        <Header>
          <a className="menu" onClick={() => this.props.history.go(-1)}>
            <i className="icon">&#xe60e;</i>
          </a>
          <Title title="" />
        </Header>

        <Content>
          {coupon &&
            <div className="list compact overlap">
              <div className="item">
                <div className="text">
                  <p className="text-justify">
                    <span className="label"></span>
                    <span className="value">{STATUS[coupon.status] || ''}</span>
                  </p>
                </div>
              </div>
              <div className="item">
                <div className="text">
                  <p className="text-justify">
                    <span className="label text-gray"></span>
                    <span className="value">NO.{coupon.code}</span>
                  </p>
                  <p className="text-justify">
                    <span className="label text-gray"></span>
                    <span className="value text-driving">
                      <span className="text-xs"></span>{coupon.price}
                    </span>
                  </p>
                  <p className="text-justify">
                    <span className="label text-gray"></span>
                    <span className="value">{TYPES[coupon.templateType] || ''}</span>
                  </p>
                  <p className="text-justify">
                    <span className="label text-gray"></span>
                    <span className="value">
                      {Filter.date(coupon.startTime, 'yyyy/MM/dd')} - {Filter.date(coupon.endTime, 'yyyy/MM/dd')}
                    </span>
                  </p>
                </div>
              </div>
            </div>
          }

          {coupon &&
            <article className="article">
              <h3></h3>
              <section className="text-darkgray">
                {this.renderSesc()}
              </section>
            </article>
          }
        </Content>
      </View>
    );
  }
}

export default Coupon;



